import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/auth'

import { ref, onUnmounted } from 'vue'

const config = {
  apiKey: process.env.VUE_APP_API_KEY,
  authDomain: process.env.VUE_APP_AUTH_DOMAIN,
  projectId: process.env.VUE_APP_PROJECT_ID,
  storageBucket: process.env.VUE_APP_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_MESSANGING_SENDER,
  appId: process.env.VUE_APP_APP_ID
}

const firebaseApp = firebase.initializeApp(config)

const db = firebaseApp.firestore()
const usersCollection = db.collection('users')
const postsCollection = db.collection('posts')

export const createUser = user => {
  return firebaseApp.auth().createUserWithEmailAndPassword(user.email, user.password)
}

export const loginUser = user => {
  return firebaseApp.auth().signInWithEmailAndPassword(user.email, user.password)
}

export const fetchPosts = () => {
  const posts = ref([])

  const close = postsCollection.onSnapshot(snapshot => {
    posts.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
  })

  onUnmounted(close)

  return posts
}

export const createPost = post => {
  return postsCollection.add(post)
}

export const fetchPost = async slug => {
  var post = ref(null)
  
  return await postsCollection.where('slug', '==', slug).get().then((snapshot) => {
      post.value = snapshot.docs[0]

      return post.value ? post.value.data() : null
  })
}


export const updatePost = (slug, upPost) => {
  var post = ref(null)

  return postsCollection.where('slug', '==', slug).get().then((snapshot) => {
      post.value = snapshot.docs[0].ref.update(upPost)

      return post.value ? post.value : null
  })
}

export const deletePost = slug => {
  var post = ref(null)
  
  return postsCollection.where('slug', '==', slug).get().then((snapshot) => {
      post.value = snapshot.docs[0].ref.delete()

      return post.value ? post.value : null
  })
}



export const getUser = async id => {
  const user = await usersCollection.doc(id).get()
  return user.exists ? user.data() : null
}

export const updateUser = (id, user) => {
  return usersCollection.doc(id).update(user)
}

export const deleteUser = id => {
  return usersCollection.doc(id).delete()
}

export const getCurrentUser = () => {
  var isLoggedIn = ref(null)

  firebase.auth().onAuthStateChanged((user) => {
      isLoggedIn.value = user
  })

  return isLoggedIn
}
